@import url(https://fonts.googleapis.com/css2?family=Ubuntu:wght@300;400;500;700&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    -webkit-animation: App-logo-spin infinite 20s linear;
            animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@-webkit-keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

@keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

/* Global Styles Start Here */

* {
    margin: 0;
    padding: 0;
    box-sizing: border-box;
    font-family: 'Ubuntu', sans-serif;
}

body {
    display: flex;
    justify-content: center;
    align-items: center;
    min-height: 100vh;
    background: url(/React-Movie-Card/static/media/bg.8a31248f.jpg);
    background-position: center;
    background-repeat: no-repeat;
    background-size: cover;
}
/* Global Styles end Here */

/* Movie Card General Styles Start Here */
.card {
    position: relative;
    width: 240px;
    height: 356px;
    height: auto;
    border: none;
    box-shadow: 10px 30px 30px rgba(0,0,0,0.9);
    overflow: hidden;
}

.card .content {
    display: flex;
    align-items: center;
}

.card .content .imgBx {
    position: relative;
    width: 240px;
    height: 356px;
    overflow: hidden;
}

.card .content .imgBx img {
    width: 240px;
    height: 356px;
}
/* Movie Card General Styles End Here */

/* Movie Details Section Styles Start Here */
.details {
    position: relative;
    display:inline-block;
    justify-content: center;
    background: rgba(0,0,0,0.8);
    width: 240px;
}

.details ion-icon {
    font-size: 1.1em;
    color: red;
    padding-right: .5em;
    padding-left: .9em;
}

.details li {
    position: relative;
    display: flex;
    color: #fff;
    text-align: start;
    list-style: none;
    margin: 12px 0;
}
/* Movie Details Section Styles End Here */

/* Details Toggle Styles Start Here */
.toggle {
    position: absolute;
    bottom: 0;
    right: 0;
    width: 40px;
    height: 40px;
    background: rgba(255,255,255,0.4);
    display: flex;
    justify-content: center;
    align-items: center;
    cursor: pointer;
    z-index: 100;
    border: none;
}

.toggle ion-icon {
    color: red;
    font-size: 1.4em;
    transition: -webkit-transform ease-in-out .25s;
    transition: transform ease-in-out .25s;
    transition: transform ease-in-out .25s, -webkit-transform ease-in-out .25s;
}
/* Details Toggle Styles End Here */
